export function loadSkin(skin: string) {
  switch (skin) {
    case 'afternoon':
      import('./afternoon.css');
      break;
    case 'sunset':
      import('./sunset.css');
      break;
    case 'morning':
      import('./morning.css');
      break;
    case 'minimalist':
      import('./minimalist.css');
      break;
    case 'business':
      import('./business.css');
      break;
    default:
      import('./night.css');
  }
}
